@use "../base/variables" as v;
@use "../base/mixins" as m;
.contenido-menu {
  background-image: url(../img/cafe.jpg);
  background-repeat: no-repeat;
  background-position: center bottom;
  aspect-ratio: 3 / 6;
  @include m.telefono {
    aspect-ratio: 3/4;
  }

  @include m.tablet {
    aspect-ratio: 16 / 6;
    background-size: 40%;
    background-position: bottom right;
    @include m.grid(2, v.$separacion);
  }
  .texto{
    font-size: 2rem;
    text-align: justify;
  }
}

